import Footer from "modules/footer/Footer";
import Header from "modules/header/Header";
import React from "react";
import { Outlet } from "react-router-dom";

const LayoutShopee = () => {
  return (
    <div className="flex flex-col min-h-screen overflow-hidden bg-whiteSmoke">
      <Header></Header>
      <div className="flex-1">
        <Outlet></Outlet>
      </div>
      <Footer></Footer>
    </div>
  );
};

export default LayoutShopee;
